import * as Yup from 'yup';
import * as React from 'react';
import PropTypes from 'prop-types';
import { useCallback, useEffect, useState } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
import { Form, FormikProvider, useFormik } from 'formik';
// material
import {
  Box,
  Card,
  Grid,
  Stack,
  TextField,
  IconButton,
  InputAdornment,
  FormHelperText,
  FormControlLabel,
  Switch
} from '@mui/material';
import { LoadingButton } from '@mui/lab';
import { DatePicker } from '@mui/x-date-pickers';
import { Icon } from '@iconify/react';
import eyeFill from '@iconify/icons-eva/eye-fill';
import closeFill from '@iconify/icons-eva/close-fill';
import eyeOffFill from '@iconify/icons-eva/eye-off-fill';
import { styled } from '@mui/material/styles';
import Request from '../../../utils/Request';
// routes
import { PATH_DASHBOARD } from '../../../routes/paths';
//
import Label from '../../Label';
import { UploadAvatar } from '../../upload';
import { useDispatch, useSelector } from '../../../redux/store';
import { getMessages, getCountries, getCity } from '../../../redux/slices/user';
import { fDate } from '../../../utils/formatTime';
import useAuth from '../../../hooks/useAuth';
// ----------------------------------------------------------------------

MessageNewForm.propTypes = {
  isEdit: PropTypes.bool,
  currentMessage: PropTypes.object
};

const LabelStyle = styled('div')(({ theme }) => ({
  ...theme.typography.subtitle2,
  minWidth: 160
}));

export default function MessageNewForm({ isEdit, currentMessage }) {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { user } = useAuth();
  const { enqueueSnackbar } = useSnackbar();
  const { countries, city } = useSelector((state) => state.user);

  const WhO = [
    { id: 1, code_name: 'AGAD', name: 'Agent & Admin' },
    { id: 2, code_name: 'AD', name: 'Admin' },
    { id: 3, code_name: 'AG', name: 'Agent' }
  ];

  const NewMessageSchema = Yup.object().shape({
    message: Yup.string().required('message is required'),
    ...(user?.role === 'superuser' && { who: Yup.string().required('who is required') })
  });

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      who: currentMessage?.who || null,
      message: currentMessage?.text || null,
      messageId: currentMessage?.id
    },
    validationSchema: NewMessageSchema,
    onSubmit: async (values, { setSubmitting, resetForm, setErrors }) => {
      const body = {
        ...values
      };
      try {
        if (isEdit) {
          await Request.handleModifyMessage(body);
        } else {
          await Request.handleAddMessage(body);
        }
        resetForm();
        setSubmitting(false);
        enqueueSnackbar(!isEdit ? 'Créer avec succès' : 'Mettre à jour avec succès', { variant: 'success' });
        getMessages();
        navigate(PATH_DASHBOARD.user?.promote);
      } catch (error) {
        if (error?.message) {
          enqueueSnackbar(error?.message, { variant: 'error' });
        } else {
          alert(JSON.stringify(error));
        }
        setSubmitting(false);
        setErrors(error);
      }
    }
  });

  const { errors, values, touched, handleSubmit, isSubmitting, setFieldValue, getFieldProps } = formik;

  return (
    <FormikProvider value={formik}>
      <Form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={8}>
            <Card sx={{ p: 3 }}>
              <Stack spacing={3}>
                {user?.role === 'superuser' && (
                  <Stack>
                    <TextField
                      select
                      fullWidth
                      label="Pour qui?"
                      placeholder="Pour qui?"
                      {...getFieldProps('who')}
                      SelectProps={{ native: true }}
                      error={Boolean(touched.who && errors.who)}
                    >
                      <option value="" />
                      {WhO?.map((optio) => (
                        <option key={optio.code_name} value={optio.code_name}>
                          {optio.name}
                        </option>
                      ))}
                    </TextField>
                  </Stack>
                )}
                <Stack>
                  <TextField
                    fullWidth
                    multiline
                    minRows={4}
                    maxRows={4}
                    label="Message"
                    placeholder="Entrer le message..?"
                    {...getFieldProps('message')}
                    error={Boolean(touched.message && errors.message)}
                    // helperText={touched.message && errors.message}
                  />
                </Stack>

                <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
                  <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                    {!isEdit ? 'Créer ce message' : 'Sauvegarder les modifications'}
                  </LoadingButton>
                </Box>
              </Stack>
            </Card>
          </Grid>
        </Grid>
      </Form>
    </FormikProvider>
  );
}
