import { useEffect, useState } from 'react';
import { paramCase } from 'change-case';
import { useParams, useLocation } from 'react-router-dom';
// material
import { Container } from '@mui/material';
// redux
import { useDispatch, useSelector } from '../../redux/store';
import { getMessages } from '../../redux/slices/user';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import MessageNewForm from '../../components/_dashboard/message/MessageNewForm';

// ----------------------------------------------------------------------

export default function AppPromoteCreate() {
  const { themeStretch } = useSettings();
  const dispatch = useDispatch();
  const { pathname } = useLocation();
  const { messageId } = useParams();
  const { promote } = useSelector((state) => state.user);
  const isEdit = pathname.includes('edit');
  const currentMessage = promote?.find((msg) => msg?.id === Number(messageId));
  const [currentMessageLoaded, setCurrentMessageLoaded] = useState(false);
  useEffect(() => {
    if (currentMessage) {
      setCurrentMessageLoaded(true);
    }
  }, [currentMessage]);

  useEffect(() => {
    // Load data on the initial render
    dispatch(getMessages());
  }, []);

  return (
    <Page title="Message: Créer un nouvel Message | RLP Center Lotto">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading={!isEdit ? 'Créer un nouvel Message' : 'Modifier Message'}
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Message', href: PATH_DASHBOARD.user.promote },
            { name: !isEdit ? 'Nouvel Message' : 'Modification' }
          ]}
        />

        {(!isEdit || currentMessageLoaded) && <MessageNewForm isEdit={isEdit} currentMessage={currentMessage} />}
      </Container>
    </Page>
  );
}
